<template>
  <div class="home-container">
    <van-list
    v-model="loading"
    :finished="finished"
    finished-text="没有更多了"
    @load="onLoad"
    >
      <!-- <Article></Article> -->
      <Article v-for="(item,idx) in list" :key="idx" :item="item">
      </Article>
    </van-list>
  </div>
</template>
<script>
import { getHupuArticleAPI } from '@/api'
import Article from '@/components/Article.vue'
export default {
  name: 'HomeMain',
  components: {
    Article
  },
  data() {
    return {
        list: [],
        loading: false,
        finished: false,
        topicId: 177,
        page:1,
    }
  },
  computed: {},
  watch: {},
  props: {},
  methods: {
    async onLoad() {
      const { data } = await getHupuArticleAPI({
        topicId:this.topicId,
        page: this.page
      })
      this.list = data.data.topicThreads
      this.loading = false

      if(data.data.topicThreads){
        this.page++;
      }else{
        this.finished = true
      }
    },
  },
  created() {},
}
</script>

